import axios from 'axios';
import Link from 'next/link';
import { Pencil } from 'lucide-react';
import { useParams } from 'next/navigation';
import React, { useEffect, useState } from 'react';
import EditProjectModal from "./EditProjectModal";



function About() {
  const { id } = useParams(); 
  const [projectData, setProjectData] = useState<any | null>(null);
  const [token, setToken] = useState<string | null>(null);
  const [isEdit, setisEdit] = useState(false);
  

  
  useEffect(() => {
    const storedToken = localStorage.getItem("token");
    setToken(storedToken);
  }, []);

  useEffect(() => {
    const fetchProject = async () => {
      try {
        const response = await axios.get(
          `${process.env.NEXT_PUBLIC_API_URL}/projects/project/${id}`,
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );
        setProjectData(response.data);
      } catch (error) {
        console.error("Error fetching project:", error);
      }
    };
    fetchProject();
  }, [id, token, isEdit]);

  const handleClick = () => {
    setisEdit(true);
  };

  return (
    <>
    <div className="pl-6 pr-6 pb-0">
      <nav className="flex items-center justify-between mb-6 text-gray-600 text-sm">
        <div className="flex items-center space-x-2">
          <Link href="/main/dashboard" className="hover:text-gray-900">
            Dashboard
          </Link>
          <span className="text-gray-400">›</span>
          <Link href={`/main/${projectData?._id}/overview`} className="hover:text-gray-900">
            {projectData ? (
              <p className='text-gray-500'>{projectData.name}</p>
            ) : (
              <p className="text-gray-500">Loading project name...</p>
            )}
          </Link>

          <span className="text-gray-400">›</span>
          <span className="text-black">About</span>
        </div>
        <button onClick={handleClick} className='bg-[#5D56BD] flex items-center justify-center gap-2 p-2 rounded-md text-white text-center text-[15px]'><Pencil className='h-4'/> Edit Project</button>
      </nav>

      <div className='flex flex-col items-left justify-left p-5 mt-10 w-50 gap-5 bg-white rounded-md font-poppins'>
        <h2 className='text-[20px]'>About Project</h2>
        {projectData ? (
          <p className='text-gray-500'>{projectData.description}</p>
        ) : (
          <p className="text-gray-500">Loading project data...</p>
        )}
      </div>

      <div className='flex flex-col items-left justify-left p-5 mt-7 w-50 gap-5 bg-white rounded-md font-poppins'>
        <h2 className='text-[20px]'>How we collaborate?</h2>
        {projectData ? (
          <p className='text-gray-500'>{projectData.collaboration}</p>
        ) : (
          <p className="text-gray-500">Loading project data...</p>
        )}
      </div>
    </div>
    {isEdit && (
        <EditProjectModal isOpen={isEdit} onClose={() => setisEdit(false)} />
      )}
    </>
  );
}

export default About;
