


import { SetStateAction, useState } from "react";
import {Dispatch} from "react"; 
interface SortedProps{
  isOpen: boolean,
  onClose: ()=> void,
  Active: string,
  setActive: Dispatch<SetStateAction<string>>;
}
export default function Modal({ isOpen, onClose, Active, setActive }:SortedProps) {
  const [selectedOption, setSelectedOption] = useState("By original post date");

  const handleSave = () => {
    console.log("Saved option:", Active);
    setActive(selectedOption);
    onClose();
  };

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex justify-center items-center z-50">
      <div className="bg-white w-[400px] rounded-lg shadow-lg p-6">
        <h2 className="text-lg font-semibold mb-4">How should messages be sorted?</h2>
        <form>
          <div className="space-y-1">
            <label className="flex items-center space-x-2">
              <input
                type="radio"
                name="sorting"
                value="By original post date"
                checked={selectedOption === "By original post date"}
                onChange={() => setSelectedOption("By original post date")}
                className="text-purple-600 focus:ring-purple-500"
              />
              <span className="text-[#333333]">By original post date</span>
            </label>
            <p className="text-[#808080] text-sm ml-6">The most recent message will appear first, followed by older ones.</p>
            
            <label className="flex items-center space-x-2">
              <input
                type="radio"
                name="sorting"
                value="By latest comment"
                checked= {selectedOption==="By latest comment"}
                onChange={() => setSelectedOption("By latest comment")}
                className="text-[#565DBD]"
              />
              <span className="text-[#333333]">By latest comment</span>
            </label>
            <p className="text-[#808080] text-sm ml-6 mt-2">Messages with recent comments will be shown first, regardless of when the message was posted.</p>
            
            <label className="flex items-center space-x-2">
              <input
                type="radio"
                name="sorting"
                value="Alphabetically A-Z"
                checked={selectedOption === "Alphabetically A-Z"}
                onChange={() => setSelectedOption("Alphabetically A-Z")}
                className="text-[#565DBD]"
              />
              <span className="text-[#333333]">Alphabetically A-Z</span>
            </label>
            <p className="text-[#808080] text-sm ml-6">Messages will be organized alphabetically by title.</p>
          </div>
        </form>
        <p className="text-[#333333] text-sm ml-6 mt-6">Everyone on the project will see messages in this order after you save.</p>
        <div className="mt-6 flex justify-end space-x-3">
          <button
            onClick={onClose}
            className="bg-gray-300 text-gray-800 px-4 py-2 rounded-lg hover:bg-gray-400"
          >
            Never mind
          </button>
          <button
            onClick={handleSave}
            className="bg-[#5D56BD] text-white px-4 py-2 rounded-lg"
          >
            Save Change
          </button>
        </div>
      </div>
    </div>
  );
}
