'use client'
import React from 'react'
import {useState, useEffect} from 'react';
import { Settings, LogOut } from "lucide-react";
import {useRouter} from 'next/navigation';
function date() {
     const [currentTime, setCurrentTime] = useState("");
      const [isDropdownOpen, setIsDropdownOpen] = useState(false);
      const router= useRouter()
    useEffect(() => {
        const interval = setInterval(() => {
            const now = new Date();
            const formattedTime = now.toLocaleString('en-GB', {
              weekday: 'short',
              day: 'numeric',
              month: 'short',
              year: 'numeric',
              hour: 'numeric',
              minute: 'numeric',
              second: 'numeric',
            });
            setCurrentTime(formattedTime);
          }, 1000);
      
          return () => clearInterval(interval);
        }, []);


        const toggleDropdown = () => {
            setIsDropdownOpen(!isDropdownOpen);
          };


          const handleLogout = () => {
            // Clear token or any authentication data
            localStorage.removeItem("token");
        
            // Redirect to login page
            router.push("/auth/login");
          };

    const handleClick=()=>{
      router.push('/main/settings')
    }
  return (
    <div className="h-16 px-4 flex items-center justify-between">
        {/* Right side with search, datetime and profile */}
        <div className="flex items-center gap-4">
          <input
            type="text"
            placeholder="Search"
            className="w-81 px-3 py-1 text-sm bg-gray-100 rounded-md"
          />
          <span className="text-sm text-gray-600">{currentTime}</span>
          
          {/* Profile dropdown container */}
          <div className="relative">
            <div className="flex items-center bg-[#F4F3FF] rounded-full pr-2 cursor-pointer" onClick={toggleDropdown}>
              {/* Profile image container */}
              <div className="w-10 h-10 rounded-full overflow-hidden">
                <img
                  src="https://www.creativefabrica.com/wp-content/uploads/2023/02/09/Flat-People-Icon-Graphics-60534092-1.png"
                  alt="Profile"
                  className="w-full h-full object-cover"
                />
              </div>
              
              {/* Dropdown arrow */}
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className={`w-4 h-4 ml-2 transition-transform duration-200 ${
                  isDropdownOpen ? 'rotate-180 text-[#5D56BD]' : 'text-gray-600'
                }`}
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
                strokeWidth="2"
              >
                <path strokeLinecap="round" strokeLinejoin="round" d="M19 9l-7 7-7-7" />
              </svg>
            </div>

            {/* Dropdown menu */}
            {isDropdownOpen && (
              <div className="absolute right-0 mt-2 w-48 bg-white rounded-lg shadow-lg py-1 z-10">
                <button 
                  className="w-full px-4 py-2 text-sm hover:bg-gray-100 flex items-center"
                  onClick={handleClick}
                >
                <Settings className="w-4 h-4 mr-2" />
                  Setting
                </button>
                <button
                onClick={handleLogout}
                className="w-full px-4 py-2 text-left hover:bg-gray-100 flex items-center text-sm"
              >
                <LogOut className="w-4 h-4 mr-2"/> Logout
              </button>
              </div>
            )}
          </div>
        </div>
      </div>
  )
}

export default date