'use client'
import React, { useState, useMemo, useEffect } from 'react';
import Image from 'next/image';
import { Plus, X, ChevronDown } from 'lucide-react';
import { useParams } from 'next/navigation';
import axios from 'axios';
import AddToDoModal from './ToDo/AddToDoModal';
import ToDos from './ToDo/ToDos';
import useSWR, { mutate } from 'swr';
import Link from 'next/link';
const API_URL = process.env.NEXT_PUBLIC_API_URL;

interface Task {
  _id: string;
  description: string;
  createdBy: string;
  Task: string;
  dueDate: string;
  completed: boolean;
}

interface TodoList {
  _id: string;
  title: string;
  items: Task[];
}
interface Project {
  _id: string;
  name: string;
  pinned: boolean;
  archived: boolean;
}
export default function ToDo() {
  const [isCreateModalOpen, setIsCreateModalOpen] = useState(false);
  const [newListName, setNewListName] = useState('');
  const [expandedLists, setExpandedLists] = useState<string[]>([]);
  const [isTodoModal, setTodoModal] = useState(false);
  const [openTodo, setOpenTodo] = useState(false);
  const [selectedListId, setSelectedListId] = useState<string>('');
  const [isEdit, setIsEdit]= useState<boolean>(false)
  const { id: projectId } = useParams();
   const [project, setProject] = useState<Project>({
      _id: "",
      name: "",
      pinned: false,
      archived: false,
    });
  const token = localStorage.getItem("token");

  // Fetch todo lists
  const fetchTodoLists = async (url: string, token: string | null) => {
    if (!token) return [];
    try {
      const response = await axios.get(url, {
        headers: { Authorization: `Bearer ${token}` }
      });
      return response.data;
    } catch (error) {
      console.error("Error fetching todo lists:", error);
      throw error;
    }
  };
  
  useEffect(() => {
    const fetchProject = async () => {
      try {
        const response = await axios.get(
          `${process.env.NEXT_PUBLIC_API_URL}/projects/project/${projectId}`,
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );
        setProject(response.data);
      } catch (error) {
        console.error("Error fetching project:", error);
      }
    };
    fetchProject();
  }, [projectId]);
  const { data: todoLists = [], error } = useSWR<TodoList[]>(
    token ? [`${API_URL}/todolists/${projectId}`, token] : null,
    ([url, token]) => fetchTodoLists(url, localStorage.getItem('token')),
    {
      revalidateOnFocus: false,
      dedupingInterval: 2000
    }
  );

  // Create a single fetcher for todos
  const fetchTodos = async (listId: string) => {
    if (!token || !listId) return null;
    try {
      const response = await axios.get(`${API_URL}/todos/list/${listId}`, {
        headers: { Authorization: `Bearer ${token}` }
      });
      return response.data.map((todo: any) => ({
        _id: todo._id,
        description: todo.notes || 'No Description',
        createdBy: todo.createdBy.fullName || "",
        Task: todo.task || " ",
        dueDate: todo.dueDate,
        completed: todo.status === 'Completed'
      }));
    } catch (error) {
      console.error('Error fetching todos:', error);
      return null;
    }
  };

  // Create a single SWR hook for the currently expanded list
  const { data: expandedListsTodos } = useSWR<Record<string, Task[]>>(
    expandedLists.length > 0 ? ['todos', expandedLists, token] : null,
    async () => {
      const results: Record<string, Task[]> = {};
      await Promise.all(
        expandedLists.map(async (listId) => {
          results[listId] = await fetchTodos(listId) || [];
        })
      );
      return results;
    },
    {
      revalidateOnFocus: false,
      dedupingInterval: 2000
    }
  );

  const handleCreateList = async () => {
    if (!newListName.trim()) return;
    try {
      await axios.post(
        `${API_URL}/todolists`,
        { title: newListName, projectId },
        { headers: { Authorization: `Bearer ${token}` } }
      );
      mutate([`${API_URL}/todolists/${projectId}`, token]);
      setNewListName('');
      setIsCreateModalOpen(false);
    } catch (error) {
      console.error('Error creating todo list:', error);
    }
  };

  const toggleTaskCompletion = async (todoId: string, listId: string, currentStatus: boolean) => {
    try {
      const newStatus = currentStatus ? 'Pending' : 'Completed';
      await axios.patch(
        `${API_URL}/todos/toogleTodo/${todoId}`,
        { status: newStatus },
        { headers: { Authorization: `Bearer ${token}` } }
      );
      mutate([`${API_URL}/todolists/${projectId}`, token]);
      mutate(['todos', expandedLists, token]);
    } catch (error) {
      console.error('Error updating todo status:', error);
    }
  };

  const handleDeleteTodo = async (todoId: string) => {
    try {
      await axios.delete(`${API_URL}/todos/${todoId}`, {
        headers: { Authorization: `Bearer ${token}` }
      });
      mutate([`${API_URL}/todolists/${projectId}`, token]);
      mutate(['todos', expandedLists, token]);
    } catch (error) {
      console.error('Error deleting todo:', error);
    }
  };

  const toggleExpandList = (id: string) => {
    setExpandedLists(prev =>
      prev.includes(id) ? prev.filter(listId => listId !== id) : [...prev, id]
    );
  };

  // Get todos for a specific list
  const getListTodos = (listId: string) => {
    return expandedListsTodos?.[listId] || [];
  };

  if (openTodo) {
    return <ToDos isOpen={openTodo} onClose={() => setOpenTodo(false)} />;
  }

  return (
    <div className="min-h-screen bg-[#F8F7FA]">
      <div className="pl-6 pr-6 pb-0">
            <nav className="flex items-center justify-between mb-6 text-gray-600 text-sm">
         
              <div className="flex items-center space-x-2">
                <Link href="/main/dashboard" className="hover:text-gray-900">
                  Dashboard
                </Link>
                <span className="text-gray-400">›</span>
                <Link href="/projects" className="hover:text-gray-900">
                  {project?.name}
                </Link>
                <span className="text-gray-400">›</span>
                <span className="text-black">Overview</span>{" "}
       
              </div>
            </nav>
          </div>
      <div className="p-6">
        <button
          className="flex items-center px-6 py-2 bg-[#5D56BD] text-white rounded-lg"
          onClick={() => setIsCreateModalOpen(true)}
        >
          <Plus className="mr-2" /> Create List
        </button>

        {isTodoModal && (
          <AddToDoModal
            isOpen={isTodoModal}
            onClose={() => {
              setTodoModal(false);
              mutate([`${API_URL}/todolists/${projectId}`, token]);
              mutate(['todos', expandedLists, token]);
            }}
            listId={selectedListId}
          />
        )}

        {isCreateModalOpen && (
          <div className="bg-white m-2 p-4 rounded-xl">
            <div className="flex items-center justify-between">
              <div className="text-lg font-semibold">Create List</div>
              <button onClick={() => setIsCreateModalOpen(false)} className="text-gray-500 hover:text-gray-700">
                <X className="h-6 w-6" />
              </button>
            </div>
            <div className="mt-4">
              <input
                type="text"
                value={newListName}
                onChange={(e) => setNewListName(e.target.value)}
                className="w-full border-2 border-gray-300 rounded-lg p-2 mb-2"
                placeholder="Enter list name"
              />
              <button
                className="px-6 py-2 bg-[#5D56BD] text-white rounded-lg hover:bg-[#4b479c]"
                onClick={handleCreateList}
              >
                Create
              </button>
            </div>
          </div>
        )}

        {todoLists.length > 0 ? (
          <div>
            {todoLists.map(list => {
              const todos = expandedLists.includes(list._id) ? getListTodos(list._id) : [];
              
              return (
                <div key={list._id} className="bg-white m-2 p-4 rounded-xl">
                  <div className="flex items-center justify-between">
                    <div className="flex items-center text-black font-semibold">
                      <button
                        onClick={() => toggleExpandList(list._id)}
                        className={`transition-transform ${expandedLists.includes(list._id) ? 'rotate-180' : ''}`}
                      >
                        <ChevronDown />
                      </button>
                      {list.title}
                    </div>
                    <button
                      className="flex items-center px-6 py-2 bg-[#5D56BD] text-white rounded-lg hover:bg-[#4b479c]"
                      onClick={() => {
                        setSelectedListId(list._id);
                        setTodoModal(true);
                      }}
                    >
                      <Plus className="mr-2" /> Add Todo
                    </button>
                  </div>

                  {expandedLists.includes(list._id) && (
                    <>
                      <div className="mt-4">
                        <h4 className="font-semibold text-gray-700 mb-2">Pending Tasks</h4>
                        {todos.filter(task => !task.completed).map(task => (
                          <div key={task._id} className="flex items-center gap-4 p-4 border-b">
                            <input
                              type="checkbox"
                              checked={task.completed}
                              onChange={() => toggleTaskCompletion(task._id, list._id, task.completed)}
                              className="w-5 h-5 border-2 border-gray-300 rounded"
                            />
                            <div className="flex-grow">
                              <p className="text-sm">{task.Task}</p>
                              <div className="text-sm text-gray-500 mt-1">
                                <span><img src="/api/placeholder/32/32" alt="user" /></span>
                                <span>{task?.createdBy}</span>
                                {" "} Due date: {new Date(task.dueDate).toLocaleDateString('en-GB', {
                                  day: '2-digit',
                                  month: 'short',
                                  year: 'numeric',
                                })}
                              </div>
                            </div>
                            <div className="flex gap-2">
                              {/* <button onClick={() => setOpenTodo(true)}>
                                <Image alt="edit" src="/assets/Edit Button.png" width={16} height={16} />
                              </button> */}
                              <button onClick={() => handleDeleteTodo(task._id)}>
                                <Image alt="delete" src="/assets/myProjects/Trash Icon.png" width={16} height={16} />
                              </button>
                            </div>
                          </div>
                        ))}
                      </div>

                      <div className="mt-4">
                        <h4 className="font-semibold text-gray-700 mb-2">Completed Tasks</h4>
                        {todos.filter(task => task.completed).map(task => (
                          <div key={task._id} className="flex items-center gap-4 p-4 border-b">
                            <input
                              type="checkbox"
                              checked={task.completed}
                              onChange={() => toggleTaskCompletion(task._id, list._id, task.completed)}
                              className="w-5 h-5 border-2 border-gray-300 rounded"
                            />
                            <div className="flex-grow">
                              <p className="text-sm">{task.description}</p>
                              <div className="text-sm text-gray-500 mt-1">
                                {" "} Due date: {new Date(task.dueDate).toLocaleDateString('en-GB', {
                                  day: '2-digit',
                                  month: 'short',
                                  year: 'numeric',
                                })}
                              </div>
                            </div>
                          </div>
                        ))}
                      </div>
                    </>
                  )}
                </div>
              );
            })}
          </div>
        ) : (
          <div className="flex flex-col items-center justify-center py-8">
            <Image
              src="/assets/Campfire Illustration.png"
              alt="Empty Status"
              width={400}
              height={400}
            />
            <button
              className="flex items-center px-6 py-2 bg-[#5D56BD] text-white rounded-lg"
              onClick={() => setIsCreateModalOpen(true)}
            >
              Create your first ToDo List
            </button>
          </div>
        )}
      </div>
    </div>
  );
}