import React, { useState, useEffect } from 'react';
import { X, Link, RotateCcw, Paperclip } from 'lucide-react';
import axios from 'axios';
import { useParams } from "next/navigation";
import avtar from '../../../../public/assets/avtar.jpg'
import TextareaBox from '../Textarea/TextareaBox';
import Image from 'next/image';
import {toast} from 'react-toastify';
import Toast from '@/components/auth/Toast';
interface TeamMember {
  id: string;
  name: string;
  role: string;
  avtar: string;
}

interface AddToDoModalProps {
  isOpen: boolean;
  onClose: () => void;
  listId: string;
}

const AddToDoModal = ({ isOpen, onClose, listId}: AddToDoModalProps) => {
  const [teamMembers, setTeamMembers]= useState<TeamMember[]>([]);
  const [selectedAssignee, setSelectedAssignee] = useState<TeamMember | null>(null);
  const {id}= useParams();
  const [notifyMembers, setNotifyMembers] = useState<TeamMember[]>([]);
  const [description, setDescription] = useState('');
  const [dueDate, setDueDate] = useState('');
  const [notes, setNotes] = useState('');

  const handleMemberClick = (member: TeamMember) => {
    setSelectedAssignee(member);
    if (!notifyMembers.some(m => m.id === member.id)) {
      setNotifyMembers([...notifyMembers, member]);
    }
  };
  useEffect(() => {
    const fetchUsers = async () => {
      const projectId = id;
      try {
        const response = await axios.get(`${process.env.NEXT_PUBLIC_API_URL}/projects/${projectId}/members`, {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      setTeamMembers([...response.data.members])
      } catch (error) {
        console.error("Error fetching users:", error);
      }
    };
    fetchUsers();
  }, [id]);
  const removeNotifyMember = (memberId: string) => {
    setNotifyMembers(notifyMembers.filter(member => member.id !== memberId));
  };

  const handleSubmit = async() => {
    if(!description || !dueDate || !selectedAssignee|| !notifyMembers || !notes){
      toast.error("Please fill all fields")
      return
    }
    const formData = {
      todoListId: listId,
      task: description,
      projectId: id,
      dueDate,
      assignedTo: selectedAssignee,
      notifyUsers: notifyMembers,
      notes,
    };

    try {
      const response = await axios.post(`${process.env.NEXT_PUBLIC_API_URL}/todos`, {formData},{
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      }
    );
    // console.log(response)
    } catch (error) {
      console.error("Error fetching users:", error);
    }
    setDescription('');
    setDueDate('');
    setSelectedAssignee(null);
    setNotifyMembers([]);
    setNotes('');
    onClose();
  };
  if (!isOpen) return null;

 
  return (
    <>
    {/* {console.log(listId, "jhfd")} */}
    <Toast/>

    <div className="z-10 fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4">
      <div className="bg-white rounded-lg  ">
        {/* Header */}
        <div className="flex justify-between items-center p-6">
          <h2 className="text-xl font-semibold">Add To-Do</h2>
          <button onClick={onClose} className="p-1 hover:bg-gray-100 rounded-full">
            <X className="w-5 h-5" />
          </button>
        </div>

        <div className="grid grid-cols-2 gap-6 p-6">
          {/* Left Column */}
          <div className="space-y-6">
            {/* To-Do Description */}
            <div className="space-y-2">
              <label className="block text-sm font-medium text-gray-700">To-Do</label>
              <input
                type="text"
                placeholder="Describe..."
                className="w-full p-2 border rounded-lg"
                value={description}
                onChange={(e) => setDescription(e.target.value)}
              />
            </div>

            {/* Due Date */}
            <div className="space-y-2">
              <label className="block text-sm font-medium text-gray-700">Due Date</label>
              <input
                type="date"
                className="w-full p-2 border rounded-lg"
                value={dueDate}
                onChange={(e) => setDueDate(e.target.value)}
              />
            </div>

            {/* Assigned To */}
            <div className="space-y-2">
              <label className="block text-sm font-medium text-gray-700">Assigned to</label>
              <div className="">
                <input
                  type="text"
                  placeholder="Search team member"
                  className="w-full p-2 border rounded-lg"
                />
                <div className="mt-2 space-y-2 max-h-48 overflow-y-auto">
                  {teamMembers.map((member) => (
                    <div
                      key={member.id}
                      className="flex items-center p-2 hover:bg-gray-50 rounded-lg cursor-pointer"
                      onClick={() => handleMemberClick(member)}
                    >
                      <Image  alt="avtar" width={26} height={26} src={avtar} className="rounded-full" />
                      <div className="ml-3">
                        <div className="font-medium">{member.name}</div>
                        <div className="text-sm text-gray-500">{member.role}</div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>

          {/* Right Column */}
          <div className="space-y-6">
            {/* When done, notify */}
            <div className="space-y-2">
              <label className="block text-sm font-medium text-gray-700">When done, notify</label>
              <div className="">
                <input
                  type="text"
                  placeholder="Search team member"
                  className="w-full p-2 border rounded-lg focus:ring-2"
                />
              </div>
              <div className="flex flex-wrap gap-2 mt-2">
                {notifyMembers.map((member) => (
                  <div
                    key={member.id}
                    className="flex items-center gap-2 bg-[#5D56BD] text-white px-3 py-1 rounded-full"
                  >
                    <Image src={avtar} alt="" className="w-6 h-6 rounded-full" />
                    <span>{member.name}</span>
                    <button
                      onClick={() => removeNotifyMember(member.id)}
                      className="hover:bg-purple-200 bg-[#5D56BD] rounded-full p-1"
                    >
                      <X className="w-4 h-4" />
                    </button>
                  </div>
                ))}
              </div>
            </div>

            {/* Notes */}
            <div className="space-y-2">
              <label className="block text-sm font-medium text-gray-700">Notes (Optional)</label>
              <TextareaBox notes={notes} setNotes={setNotes}/>
            </div>
          </div>
        </div>

        {/* Footer */}
        <div className="flex justify-end gap-3 py-6 px-8">
          <button
            onClick={onClose}
            className="px-6 py-2 bg-[#999999] text-white hover:bg-gray-600  rounded-lg"
          >
            Cancel
          </button>
          <button
            onClick={handleSubmit}
            className="px-4 py-2 bg-[#5D56BD] text-white rounded-lg hover:bg-[#4b479c]"
          >
            Add To-Do
          </button>
        </div>
      </div>
    </div>
    </>
  );
};

export default AddToDoModal;