const NoticeBoardModal = ({
  isOpen,
  onClose,
  messageOptions,
  setMessagesOptions,
  NoticeBoardData,
  setNoticeBoardData
}: AddToDoModalProps) => {
  const [selectedAssignee, setSelectedAssignee] = useState<TeamMember | null>(
    null
  );
  const [teamMembers, setTeamMembers] = useState<TeamMember[]>([]);
  const [notifyMembers, setNotifyMembers] = useState<TeamMember[]>(teamMembers);
  const [title, setTitle] = useState("");
  const [notes, setNotes] = useState("");
  const [select, setSelect] = useState("");
  const [drop, setDrop] = useState<boolean>(false);
  const [searchTerm, setSearchTerm] = useState("");
  const projectId = useParams().id;

  useEffect(() => {
    try {
      const fetchMembers = async () => {
        const response = await axios.get(
          `${process.env.NEXT_PUBLIC_API_URL}/projects/${projectId}/members`,
          {
            // params: { projectId },
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );
        // console.log(response.data.members);
        // setNotifyMembers(response.data.members);
        setTeamMembers(response.data.members);
        setNotifyMembers(response.data.members);
      };

      fetchMembers();
    } catch (error) {
      console.error(error);
    }
  }, []);

  const filteredMembers = useMemo(() => {
    console.log(teamMembers);
    return teamMembers.filter((member) =>
      member?.name?.toLowerCase().includes(searchTerm.toLowerCase())
    );
  }, [searchTerm]);

  const handleMemberClick = (member: TeamMember) => {
    setSelectedAssignee(member);
    if (!notifyMembers.some((m) => m.id === member.id)) {
      setNotifyMembers([...notifyMembers, member]);
    }
    setSearchTerm("");
  };

  const removeNotifyMember = (memberId: string) => {
    setNotifyMembers(notifyMembers.filter((member) => member.id !== memberId));
  };

  const handleMessageOptionClick = (option: string) => {
    setSelect(option);
    setDrop(false);
  };

  const handleAddCategory = async (category: string) => {
    try {
      const response = await axios.post(
        `${process.env.NEXT_PUBLIC_API_URL}/categories`,  // Replace with actual API URL
        { category },  // Request body
        {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem("token")}`,  // Include token in Authorization header
          },
        }
      );
  
      console.log("Category added successfully:", response.data);
      return response.data; // Return response for further use
    } catch (error) {
      if (axios.isAxiosError(error)) {
        console.error("Error adding category:", error.response?.data || error.message);
      } else {
        console.error("Error adding category:", error);
      }
      throw error;
    }
  }

  const handleSubmit = async () => {
    console.log("Title:", title);
    console.log(
      "Assigned To:",
      selectedAssignee ? selectedAssignee.name : "No assignee selected"
    );
    console.log(
      "Notify Members:",
      notifyMembers.map((member) => member.name)
    );
    console.log("Category:", select);
    console.log("Notes:", notes);

    const formData = {
      title,
      description: notes, // Assuming `notes` is the description
      category: select,
      // notifyWhenPosted: selectedAssignee ? selectedAssignee.id : null,
      notifyWhenPosted: notifyMembers.map((member) => member.id),
    
      // postedBy: userId, // Make sure to replace `userId` with the actual user ID
      comments: [], // You can initialize comments if needed, or leave empty
      project: projectId, // Replace with the actual project ID if needed
    };

    try {
      const response = await axios.post(
        `${process.env.NEXT_PUBLIC_API_URL}/noticeboard`,
        formData,
        {
          headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
        }
      );
      if (response.status === 201) {
        toast.success("Noticeboard created successfully");
        console.log("Noticeboard created successfully:", response);
        setNoticeBoardData([...NoticeBoardData, response.data.noticeboard]);
        setTimeout(() => {
          onClose(); // Close the modal after successful creation
        }, 3000);
      }
    } catch (error) {
      console.error("Failed to create project:", error);
    }
  };
  const [selectOption, setselectOption] = useState("null");

  const [isAddingCategory, setIsAddingCategory] = useState(false);
  const [newCategory, setNewCategory] = useState("");
  if (!isOpen) return null;

  return (
    // console.log(messageOptions),
    (
      <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
        <Toast/>
        <div className="bg-white rounded-lg w-full max-w-6xl">
          {/* Header */}
          <div className="flex justify-between items-center p-6 border-b">
            <h2 className="text-xl font-semibold">Add Notice Board Message</h2>
            <button
              onClick={onClose}
              className="p-1 hover:bg-gray-100 rounded-full"
            >
              <X className="w-5 h-5" />
            </button>
          </div>

          <div className="grid grid-cols-2 gap-6 p-6">
            {/* Left Column */}
            <div className="space-y-6">
              {/* To-Do Description */}
              <div className="space-y-2">
                <label className="block text-sm font-medium text-gray-700">
                  Message Title
                </label>
                <input
                  type="text"
                  placeholder="Describe..."
                  className="w-full p-2 border rounded-lg"
                  value={title}
                  onChange={(e) => setTitle(e.target.value)}
                />
              </div>
              {/* Category Select */}
              <div className="relative space-y-2">
                <label className="block text-sm font-medium text-[#333333]">
                  Pick a category (optional)
                </label>
                <div className=" absolute w-full z-50 bg-white">
                  <div className="flex flex-row w-full">
                    <button
                      className="flex flex-row w-full px-6 py-2 text-sm border border-gray-200 rounded-lg bg-white"
                      onClick={() => setDrop((prev) => !prev)}
                    >
                      {select || "None"}
                      <ChevronDown className="w-4 h-4 ml-auto" />
                    </button>
                  </div>
                  {drop && (
                    <div>
                      {messageOptions.map((option) => (
                        <div key={option}>
                          <div
                            className="px-4 py-2 text-sm hover:bg-[#565DBD] hover:text-[white] cursor-pointer"
                            onClick={() => handleMessageOptionClick(option)}
                          >
                            {option}
                          </div>
                        </div>
                      ))}
                      <div>
                        {/* Add categories button */}
                        {!isAddingCategory && (
                          <button
                            className="px-4 text-[#5D56BD] cursor-pointer"
                            onClick={() => setIsAddingCategory(true)}
                          >
                            Add categories
                          </button>
                        )}

                        {/* Input for adding a new category */}
                        {isAddingCategory && (
                          <div className="flex items-center gap-2">
                            <input
                              type="text"
                              className="px-4 py-2 border border-gray-200 rounded-lg"
                              value={newCategory}
                              onChange={(e) => setNewCategory(e.target.value)}
                              placeholder="Enter new category"
                            />
                            <button
                              onClick={() => {
                                if (newCategory.trim()) {
                                  handleAddCategory(newCategory);
                                  setMessagesOptions([
                                    ...messageOptions,
                                    newCategory,
                                  ]);
                                  setNewCategory(""); // Reset input field
                                  setIsAddingCategory(false); // Close input field
                                }
                              }}
                              className="bg-[#6BBD56] text-white px-3 py-2 rounded-r hover:bg-green-600 ml-auto"
                            >
                              ✔
                            </button>
                            <button
                              onClick={() => {
                                setNewCategory(""); // Reset input field
                                setIsAddingCategory(false); // Close input field
                              }}
                              className="bg-[#FF0000] ml-2 px-3 py-2 text-white hover:bg-red-700"
                            >
                              ✖
                            </button>
                          </div>
                        )}
                      </div>
                    </div>
                  )}
                </div>
              </div>

              {/* Notify Members */}
              <div className="relative space-y-2">
                <label className="mt-16 block text-sm font-medium text-[#333333]">
                  When done, notify
                </label>
                <div className="">
                  <div className="absolute w-auto left-0 pl-3 flex items-center pointer-events-none">
                    <Search className="mt-3 w-4 h-4 text-gray-400" />
                  </div>
                  <input
                    type="text"
                    placeholder="Search team member"
                    className="w-full p-2 pl-10 border rounded-lg focus:ring-2"
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                  />
                </div>

                {/* Filtered Member List */}
                {searchTerm && (
                  <div className=" absolute z-10 w-full bg-white rounded-lg shadow-lg max-h-60 overflow-y-auto">
                    {filteredMembers.map((member) => (
                      <div
                        key={member.id}
                        className="flex items-center p-2 hover:bg-gray-100 cursor-pointer"
                        onClick={() => handleMemberClick(member)}
                      >
                        <img
                          src={member.avatar}
                          alt=""
                          className="w-8 h-8 rounded-full mr-2"
                        />
                        <div>
                          <div className="font-medium">{member.name}</div>
                          <div className="text-sm text-gray-500">
                            {member.role}
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                )}

                <div className="flex flex-wrap gap-2 mt-2">
                  {notifyMembers.map((member) => (
                    <div className="border-solid">
                    <div
                      key={member.id}
                      className="py- 4 flex items-center gap-2 bg-[#5D56BD] text-white px-3 py-1 rounded-full"
                    >
                      <img
                        src={member.avatar}
                        alt=""
                        className="w-6 h-6 rounded-full"
                      />
                      <span>{member.name}</span>
                      <button
                        onClick={() => removeNotifyMember(member.id)}
                        className="hover:bg-purple-200 bg-[#5D56BD] rounded-full p-1"
                      >
                        <X className="w-4 h-4" />
                      </button>
                    </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>

          {/* Right Column */}
          <div className="space-y-6">
            {/* Notes */}
            <div className="space-y-2">
              <label className="block text-sm font-medium text-gray-700">Notes (Optional)</label>
              
              <TextareaBox notes={notes} setNotes={setNotes}/>
            </div>
          </div>
        </div>

          {/* Footer */}
          <div className="flex justify-end gap-3 p-6 border-t">
            <button
              onClick={handleSubmit}
              className="px-4 py-2 text-white bg-[#5D56BD] rounded-lg"
            >
              Post this message
            </button>
            <button
              onClick={onClose}
              className="px-4 py-1 border-[#6BBD56] border-2 text-[#6BBD56] rounded-lg hover:bg-green-600 hover:text-white"
            >
              Save a draft
            </button>
          </div>
        </div>
      </div>
    )
  );
};

export default NoticeBoardModal;
