import React, { useState, useEffect } from "react";
import Image from "next/image";
import googleMeet from "../../../../public/assets/Google Meet image.png";
import avtar from "../../../../public/assets/avtar.jpg";
import TextareaBox from "../Textarea/TextareaBox";
import Link from "next/link";
import axios from "axios";

interface DetailProps {
  id: string;
  isOpenDetail: boolean;
  onClose: () => void;
}

const NoticeDetail = ({ id, isOpenDetail, onClose }: DetailProps) => {
  interface NoticeData {
    postedBy?: { fullName: string };
    postedOn?: string;
    category?: string;
    title?: string;
    description?: string;
    notifyWhenPosted?: { fullName: string }[];
    comments?: {
      avatar: string;
      commentedBy: { fullName: string; role: string };
      commentedOn: string;
      comment: string;
    }[];
  }

  const [noticeData, setNoticeData] = useState<NoticeData>({});
  const [comment, setComment] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchNotes = async () => {
      try {
        const response = await axios.get(
          `${process.env.NEXT_PUBLIC_API_URL}/noticeboard/${id}`,
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );
        console.log(response.data, "notice data");
        setNoticeData(response.data);
      } catch (error) {
        console.error("Error fetching notice data:", error);
      }
    };

    fetchNotes();
  }, [id]);

  const handlePostComment = async () => {
    if (!comment.trim()) return;
    console.log(comment);
    setLoading(true);
    setError(null);

    try {
      const response = await axios.post(
        `${process.env.NEXT_PUBLIC_API_URL}/noticeboard/${id}/comment`,
        { comment },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
            "Content-Type": "application/json",
          },
        }
      );

      // Update noticeData with new comment
      setNoticeData((prevData) => ({
        ...prevData,
        comments: [...(prevData.comments || []), response.data],
      }));
      setComment(""); // Clear input field after posting
    } catch (error) {
      if (axios.isAxiosError(error)) {
        console.error("Error posting comment:", error.response?.data || error.message);
      } else {
        console.error("Error posting comment:", error);
      }
      setError("Failed to post comment. Try again.");
    } finally {
      setLoading(false);
    }
  };

  if (!isOpenDetail) return null;

  return (
    <>
      <div className="pl-6 pr-6 pb-0">
        <nav className="flex items-center justify-between mb-6 text-gray-600 text-sm">
          <div className="flex items-center space-x-2">
            <Link href="/main/dashboard" className="hover:text-gray-900">Dashboard</Link>
            <span className="text-gray-400">›</span>
            <Link href="/projects" className="hover:text-gray-900">Project Atlas</Link>
            <span className="text-gray-400">›</span>
            <span className="text-black">Notice Board</span>
          </div>
        </nav>
      </div>
      <div>
        <button
          className="flex flex-row justify-center items-center px-6 py-2 bg-[#5D56BD] text-white rounded-lg mx-4 my-2"
          onClick={onClose}
        >
          {" <- "}Go Back
        </button>
        <div className="mx-4 my-2 p-4 bg-white rounded-lg shadow">
          <div className="mb-6">
            <div className="flex justify-between items-start">
              <div className="flex">
                <div className="mt-1 text-sm text-gray-500">
                  Produced by: <span className="font-medium">{noticeData?.postedBy?.fullName}</span> on {noticeData.postedOn}
                </div>
              </div>
            </div>
            <div className="my-3 text-gray-500">Category: {noticeData.category}</div>
            <div><h1>{noticeData.title}</h1></div>
            <div className="mt-4 gap-6">{noticeData.description}</div>
            <div className="flex justify-center my-4">
              <Image src={googleMeet} alt="googleLink" width={500} height={500} />
            </div>
            <div className="flex items-center gap-2 mt-2">
              <span className="text-sm text-gray-500">When posted, notify</span>
              <div className="flex -space-x-2 text-white">
                {noticeData.notifyWhenPosted?.map((watcher, index) => (
                  <div className="flex items-center gap-1 p-1 ml-2" key={index}>
                    <div className="flex justify-center items-center bg-[#5D56BD] rounded-full ml-2 p-2">
                      <Image src={avtar} alt="photo" className="rounded-full" width={40} height={40} />
                      <div className="text-sm font-medium text-white rounded-xl p-2">{watcher?.fullName}</div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>

        {/* Comments Section */}
        <div className="mx-4 my-4 bg-white p-4 rounded-lg">
          <h2 className="text-sm font-medium text-gray-900 mb-4">Comments</h2>

          {/* Comment Input */}
          <div className="mt-4 flex gap-3">
            <Image src="/api/placeholder/32/32" alt="User avatar" className="w-8 h-8 rounded-full" width={48} height={48} />
            <div className="flex flex-grow">
              <TextareaBox notes={comment} setNotes={setComment} />
            </div>
          </div>
          {error && <p className="text-red-500">{error}</p>}
          <button
            onClick={handlePostComment}
            className="px-4 py-2 bg-[#5D56BD] text-white rounded-lg"
            disabled={loading}
          >
            {loading ? "Posting..." : "Post Comment"}
          </button>

          {/* Display Comments */}
          <div className="space-y-4 mt-4">
            {noticeData.comments && noticeData.comments.length > 0 ? (
              noticeData.comments.map((comment, index) => (
                
                <div key={index} className="flex gap-3">
                  <div>
                    <Image src={comment?.avatar} alt={comment.commentedBy?.fullName} className="rounded-full" width={48} height={48} />
                    <div className="flex-1">
                      <div className="flex items-center gap-2">
                        <span className="font-medium text-sm">{comment.commentedBy?.fullName}</span>
                        <span className="text-xs text-gray-500">({comment.commentedBy?.role})</span>
                      </div>
                    </div>
                    {/* <div className="text-xs text-gray-500 mb-1">{comment.commentedOn}</div> */}
                    <div className="text-xs text-gray-500 mb-1">{comment.commentedOn?.split("T")[0]} {comment.commentedOn?.split("T")[1]?.split(".")[0]}
                    </div>
                    <p className="text-sm text-gray-600">{comment.comment}</p>
                  </div>
                </div>
              ))
            ) : (
              <p className="text-gray-500 text-sm">No comments yet. Be the first to comment!</p>
            )}
          </div>
        </div>
      </div>
    </>
  );
};

export default NoticeDetail;
