"use client";

import React, { useState, useEffect, useRef } from "react";
import Link from "next/link";
import Image from "next/image";
import { Milestone, Plus } from "lucide-react";
import EditProjectModal from "./EditProjectModal";
import { useSearchParams } from "next/navigation";
import axios from "axios";
import { toast } from "react-toastify";
import { useParams } from "next/navigation";
import AddMilestone from "../dashboard/AddMilestone";
import EditmileStoneModal from "./EditmileStoneModal";
import plusIcon from "../../../public/assets/Plus Icon Manage Team.png";
import AddTeamMembers from "../dashboard/AddTeamMembers";
import Pin from "../../../public/assets/Pin.png";
import PinFalse from "../../../public/assets/myProjects/PinnedFalse Icon.png";
import Archive from "../../../public/assets/Archive Button.png";
import ArchiveFalse from "../../../public/assets/Archive Icon.png";
import avtar from "../../../public/assets/avtar.jpg";
import useSWR from "swr";
import Toast from "../auth/Toast";
interface milestoneInterface {
  id: string;
  title: string;
  dueDate: string;
  assignedTo: string;
  completeDate: string;
  status: {
    type: string;
    enum: ["pending", "completed"];
    default: "pending";
  };
}
interface NewTeamMembers {
  id: string;
  name: string;
  role: string;
}
interface TeamMember {
  _id: string;
  fullName: string;
  role: string;
  position: string;
  avatar: string;
}
interface newMilestoneInterface {
  _id: number;
  description: string;
  assignedTo: string;
  dueDate: string;
}
interface ApiMember {
  id?: string;
  name?: string;
  role?: string;
}

interface ApiResponse {
  members: ApiMember[];
}

interface TransformedMember {
  _id: string;
  fullName: string;
  position: string;
  role: string;
  avatar: string; // Assuming 'avtar' is of type string
}
interface mileStoneThatIsFetchedFromBackend {
  _id: string;
  title: string;
  assignedTo: {
    _id: string;
    fullName: string;
  }[];
  dueDate: string;
  completedOn: string;
  status: string;
}
// interface Notice {
//   id: number;
//   title: string;
//   content: string;
//   Duedate: string;
//   author: string;
// }
interface Parent {
  unMount: () => void;
  Mount: () => void;
  mountCampfire: () => void;
}
interface Notice {
  _id: string;
  title: string;
  project: string;
  postedOn: string;
  postedBy: {
    id: string;
    fullName: string;
  };
  notifyWhenPosted: [];
  description: string;
  comments: string;
  category: string;
}
interface Project {
  _id: string;
  name: string;
  pinned: boolean;
  archived: boolean;
}
export default function ProjectDashboard({
  unMount,
  Mount,
  mountCampfire,
}: Parent) {
  const searchParams = useSearchParams();
  const [milestoneFromBackend, setmilestoneFromBackend] = useState<
    mileStoneThatIsFetchedFromBackend[]
  >([]);
  const [projectName, setProjectName] = useState<string | null>(null);
  const [project, setProject] = useState<Project>({
    _id: "",
    name: "",
    pinned: false,
    archived: false,
  });
  const [milestones, setMilestones] = useState<milestoneInterface[]>([]);
  const [isEdit, setisEdit] = useState(false);
  const [token, setToken]= useState<string | null>(null)
  // const [error, setError] = useState<string | null>(null);
  const [newMilestone, setNewMilestone]= useState<newMilestoneInterface[]>([])
  const [isAddTeamMemberModalOpen, setisAddTeamMemberModal]= useState<boolean>(false)
  const [isAddMilestone, setisAddMilestone]= useState<boolean>(false);
  const [NewTeamMembers, setNewTeamMembers]= useState<NewTeamMembers[]>([])
 
  const [isEditmilestoneModal, setisEditmilestoneModal]= useState<boolean>(false);
  const [milestoneValue, setmileStoneValue]= useState<string>("");
  const [mileStoneId, setmileStoneId]= useState<string>("");
  const [tempMembers, setTempMembers] = useState<TeamMember[]>([]);
  const fromOverview= true;
  const handleEditMilestone=(id: string, title: string)=>{
    setmileStoneValue(title)
    setmileStoneId(id);
    setisEditmilestoneModal(true);
  };

  useEffect(() => {
    const temp = localStorage.getItem("token");
    setToken(temp);
  }, [token]);

  
  const [teamMembers, setTeamMembers] = useState<TransformedMember[]>([]);
  const params = useParams();
  const { id } = params;
  const projectId = id;

  useEffect(() => {
    const fetchProject = async () => {
      try {
        const response = await axios.get(
          `${process.env.NEXT_PUBLIC_API_URL}/projects/project/${projectId}`,
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );
        setProject(response.data);
      } catch (error) {
        console.error("Error fetching project:", error);
      }
    };
    fetchProject();
  }, [id]);

  const handleAddMember = async (newMember: TeamMember) => {
    if (newMember.role === "Select Role") {
      toast.error("Please select a valid role");
      return;
    }

    if (isUpdating.current) return;
    isUpdating.current = true;

    try {
      const payload = {
        name: newMember.fullName,
        projectId,
        memberId: newMember._id,
        newRole: newMember.role,
      };

      const response = await axios.patch(
        `${process.env.NEXT_PUBLIC_API_URL}/projects/projects/members/change-role`,
        payload,
        {
          headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
        }
      );

      if (response.status === 200) {
        setTempMembers((prevMembers) => {
          const memberExists = prevMembers.some(
            (member) => member._id === newMember._id
          );
          if (memberExists) {
            return prevMembers.map((member) =>
              member._id === newMember._id
                ? { ...member, role: newMember.role }
                : member
            );
          }
          return [...prevMembers, newMember];
        })

        // setTeamMembers(updateMemberState);
      
        mutateMembers();
      }
    } catch (error) {
      console.error("Failed to update member:", error);
      toast.error("Failed to update team member");
    } finally {
      isUpdating.current = false;
    }
  };
  

  const isUpdating = useRef(false)
useEffect(() => {
  // console.log(NewTeamMembers, "gggg")
  if (NewTeamMembers && NewTeamMembers.length > 0) {
    
      const formattedTeamMembers = NewTeamMembers.map(member => {
          return {
              _id: member.id,
              fullName: member.name,
              position: member.role,
              role: member.role,
              avatar: "/api/placeholder"
          };
      });
      setTeamMembers(formattedTeamMembers);
    }
  }, [NewTeamMembers]);

const fetcherMembers= async([url, token]:[string, string | null])=>{
  if(!token) return [];
  try{
    const response= await axios.get(url, {headers:{Authorization: `Bearer ${token}`}});
    const transformedMembers = response.data.members.map((member:any) => ({
      _id: member.id || "",
      fullName: member.name || "",
      position: member.role || "",
      role: member.role || "",
      avatar: avtar
    }));
    setNewTeamMembers(response.data.members);
 
    setTeamMembers(transformedMembers);
      return transformedMembers;
  }catch(error){
    console.log("Error in Members function", error);
    throw error;
  }
}
const {data: datas, error:error1, mutate: mutateMembers}= useSWR(
  token ? [`${process.env.NEXT_PUBLIC_API_URL}/projects/${projectId}/members`, token]: null,
  fetcherMembers,{
    revalidateOnFocus: false,
    dedupingInterval: 2000 
  }
);
const fetcher = async ([url, token]: [string, string | null]) => {
  if (!token) return [];
  try {
    const response = await axios.get(url, { headers: { Authorization: `Bearer ${token}` } });
    setmilestoneFromBackend(response.data.milestones)
    return response.data;
  } catch (error) {
    console.error("Error fetching users", error);
    throw error;
  }
};

const { data: projects, error, mutate } = useSWR(
  token ? [`${process.env.NEXT_PUBLIC_API_URL}/projects/${projectId}/milestones`, token] : null,
  fetcher,
  { revalidateOnFocus: false,
    dedupingInterval: 2000 
   } 
);
const handleMileStoneFetch=()=>{
 
  mutate();
}

const handleClose=()=>{
  mutateMembers()
  setisAddTeamMemberModal(false)
}
const [NoticeBoardData, setNoticeBoard]= useState<Notice[]>([]);
useEffect(() => {
  const fetchNotes = async () => {
    try {
      const response = await axios.get(
        `${process.env.NEXT_PUBLIC_API_URL}/noticeboard/latest`,
        {
          params: {
            projectId: id,
          },
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      console.log(response.data, "noticeBoardData");
      setNoticeBoard([...response.data])
    } catch (error) {
      console.error(error);
    }
  };

    fetchNotes();
  }, []);

  const today = new Date().toISOString().split("T")[0];
  //UseEffect for fetching the upcoming milestones for the side bar
  useEffect(() => {
    const projectId = id;
    const getMilestone = async () => {
      try {
        const response = await axios.get(
          `${process.env.NEXT_PUBLIC_API_URL}/projects/${projectId}/milestones`,
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );
        const data = response.data;
        setmilestoneFromBackend(data.milestones);
      } catch (error) {
        console.error("Error fetching users:", error);
      }
    };
    getMilestone();
  }, []);

  const handlePinToggle = async (project: any) => {
    try {
      const updatedPinned = !project.pinned;
      let projectId = project._id;
      console.log(projectId);
      await axios.put(
        `${process.env.NEXT_PUBLIC_API_URL}/projects/pinned`,
        { pinned: updatedPinned },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
          params: { projectId: projectId },
        }
      );
      setProject((prev) => ({ ...prev, pinned: updatedPinned }));
    } catch (err) {
      console.log("Error in piiend projects", err);
    }
  };

  const handleArchiveToggle = async (project: any) => {
    try {
      const updatedArchived = !project.archived;
      let projectId = project._id;
      // console.log(projectId);
      await axios.put(
        `${process.env.NEXT_PUBLIC_API_URL}/projects/archived`,
        { archived: updatedArchived },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
          params: { projectId: projectId },
        }
      );
      setProject((prev) => ({ ...prev, archived: updatedArchived }));
    } catch (err) {
      console.log("Error in archived projects", error)
    }
  };

  const milestonesData: milestoneInterface[] = []

  useEffect(() => {
    setMilestones(milestonesData);
  }, []);
  // }
  const handleClick = () => {
    setisEdit(true);
  };
  const handleMilestoneToogle = async (milestoneId: string, status: string) => {
    const projectId = id;
    if (status == "pending") {
      status = "completed";
    } else {
      status = "pending";
    }
    const newStatus = status;
    try {
      const response = await axios.patch(
        `${process.env.NEXT_PUBLIC_API_URL}/projects/milestones/update-status`,
        { status },
        {
          params: { projectId, milestoneId },
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      // if (response.data){
      //   window.location.reload()
      // }.
      setmilestoneFromBackend((prev) =>
        prev.map((milestone) =>
          milestone._id === milestoneId
            ? { ...milestone, completedOn: today, status: newStatus }
            : milestone
        )
      );
    } catch (error) {
      console.error("Error fetching users:", error);
    }
  };
  const handleSwitch = () => {
    unMount();
    Mount();
  };
  const switchCampfire = () => {
    unMount();
    mountCampfire();
  };
  return (
    <>
    <Toast/>
    {/* {console.log(NoticeBoardData, "teamMembers")} */}
      <div>
        {/* {console.log("member", payload)} */}
        <div className="min-h-screen bg-[#F8F7FA]">
          {/* Header Section */}

          <div className="pl-6 pr-6 pb-0">
            <nav className="flex items-center justify-between mb-6 text-gray-600 text-sm">
              {/* Left aligned: Breadcrumb navigation */}
              <div className="flex items-center space-x-2">
                <Link href="/main/dashboard" className="hover:text-gray-900">
                  Dashboard
                </Link>
                <span className="text-gray-400">›</span>
                <Link href="/projects" className="hover:text-gray-900">
                  {project?.name}
                </Link>
                <span className="text-gray-400">›</span>
                <span className="text-black">Overview</span>{" "}
                {/* Display project name */}
              </div>

              {/* Right aligned: Buttons */}
              <div className="flex items-center space-x-2">
                <button
                  onClick={() => handlePinToggle(project)}
                  className="p-2 hover:bg-gray-100 rounded-lg border-2"
                >
                  <Image
                    src={project.pinned ? Pin : PinFalse}
                    alt="Pin"
                    width={24}
                    height={24}
                  />
                </button>
                <button
                  onClick={() => handleArchiveToggle(project)}
                  className="p-2 hover:bg-gray-100 rounded-lg border-2"
                >
                  <Image
                    src={project.archived ? Archive : ArchiveFalse}
                    alt="Archive"
                    width={24}
                    height={24}
                  />
                </button>
                <button
                  className="px-4 py-2 bg-[#5D56BD] text-white rounded-lg"
                  onClick={handleClick}
                >
                  Edit project
                </button>
              </div>
            </nav>
          </div>

          {/* Main Content Grid */}
          <div className="grid grid-cols-3 gap-6 p-6">
            {/* Campfire Section */}
            <div className="col-span-2 bg-white rounded-xl p-6">
              <div className="flex justify-between items-center mb-4">
                <h2 className="text-xl font-semibold">Campfire</h2>
                <button
                  className="text-indigo-600 text-sm"
                  onClick={switchCampfire}
                >
                  Show all
                </button>
              </div>
              <div className="flex flex-col items-center justify-center py-12">
                <Image
                  src="/assets/Campfire Illustration.png"
                  alt="No Messages"
                  width={120}
                  height={120}
                />
                <p className="text-gray-500 mt-4">No Messages</p>
              </div>
            </div>

          {/* Project Status Section */}
          <div className="col-span-1 row-span-2 bg-[#1C1C38] text-white rounded-xl p-6">
            <h2 className="text-xl font-semibold mb-4">Project Status</h2>
            <div className="flex justify-between items-center mb-6">
              <span>On Track</span>
              <button className="px-3 py-1 rounded-lg text-sm bg-[#13BC1A]">
                Update Status
              </button>
            </div>

            {/* Existing Milestones Section */}
            <div className="mb-6">
              {(milestoneFromBackend.length) > 0 ? (
                milestoneFromBackend.filter((milestone)=> milestone.status!=="pending").map((milestone) => (
                  
                  <div
                    key={milestone._id}
                    className="bg-white text-[#4D4D4D] p-4 mb-3 rounded-lg"
                  >
                    <div className="flex flec-row items-center mb-2">
                      <Image
                        src="/assets/avtar.jpg"
                        alt="avatar"
                        width={34}
                        height={34}
                        className=" bg-gray-200 rounded-full"
                      />
                      <h3 className="font-medium ml-2 text-[#4D4D4D]">
                        {milestone.assignedTo[0].fullName}
                      </h3>
                    </div>
                    <div className="text-sm">{milestone.title}</div>
                    <div className="text-xs text-gray-500">
                      Due Date:    {new Date(milestone.dueDate).toLocaleDateString(
                          "en-GB",
                          {
                            day: "2-digit",
                            month: "short",
                            year: "numeric",
                          }
                        )}
                    </div>
                    <div className="text-xs text-gray-500">
                      Completed Date: {
                        new Date(milestone.completedOn).toLocaleDateString(
                          "en-GB",{
                            day: "2-digit",
                            month: "short",
                            year:"numeric",
                          }
                        )
                      }
                    </div>
                  </div>
                ))
              ) : (
                <div className="flex flex-col items-center justify-center py-8">
                  <Image
                    src="/assets/Empty Project Illustration.png"
                    alt="Empty Status"
                    width={100}
                    height={100}
                  />
                  <p className="text-gray-400 mt-4">Empty Project Status</p>
                </div>
              )}
            </div>

              {/* New Timeline Section */}
              <div className="mt-8">
                <div className="flex items-center mb-4">
                  <div className="flex items-center">
                    <svg
                      className="w-5 h-5 mr-2"
                      fill="currentColor"
                      viewBox="0 0 20 20"
                    >
                      <path
                        fillRule="evenodd"
                        d="M6 2a1 1 0 00-1 1v1H4a2 2 0 00-2 2v10a2 2 0 002 2h12a2 2 0 002-2V6a2 2 0 00-2-2h-1V3a1 1 0 10-2 0v1H7V3a1 1 0 00-1-1zm0 5a1 1 0 000 2h8a1 1 0 100-2H6z"
                        clipRule="evenodd"
                      />
                    </svg>
                    <span className="font-medium">Due Date</span>
                  </div>
                  <div>
                    <span className="ml-2 text-sm text-gray-300">
                      {new Date().toLocaleDateString("en-GB", {
                        day: "2-digit",
                        month: "short",
                        year: "numeric",
                      })}
                    </span>
                  </div>
                </div>
                <div className="relative">
                  {milestoneFromBackend
                    .filter(
                      (milestone) => milestone.dueDate.substring(0, 10) == today
                    )
                    .map((task, index) => (
                      <div key={index} className="mb-6 relative pl-8">
                        {/* Timeline connector line */}
                        <div className="absolute left-3 top-0 h-full w-px bg-gray-600" />

                    {/* Timeline number indicator */}
                    <div className="absolute left-0 w-6 h-6 rounded-full bg-[#FFB13B] flex items-center justify-center text-sm">
                      {index + 1}
                    </div>
                    {/* Task content */}
                    <div className="pl-4">
                      <div className="flex items-center mb-2">
                        <Image
                          src="/assets/avtar.jpg"
                          alt="hlo"
                          width={24}
                          height={24}
                          className="rounded-full"
                        />
                        <span className="ml-2 text-sm">{task.assignedTo[0].fullName}</span>
                      </div>
                      <p className="text-sm">{task.title}</p>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>

            {/* Notice Board Section */}
            <div className="col-span-2 bg-white rounded-xl p-6">
              <div className="flex justify-between items-center mb-4">
                <h2 className="text-xl font-semibold">Notice Board</h2>
                <button
                  className="text-[#5D56BD] text-sm"
                  onClick={handleSwitch}
                >
                  Show all
                </button>
              </div>

            {NoticeBoardData.length > 0 ? (
              <div className="space-y-4">
                {NoticeBoardData.map((notice) => (
                  <div
                    key={notice._id}
                    className="border rounded-lg p-4 hover:shadow-md transition-shadow"
                  >
                    <h3 className="font-medium">{notice.title}</h3>
                    <p className="text-gray-600 text-sm mb-3">
                      {notice.description}
                    </p>
                    <div className="flex items-center text-sm text-gray-500">
                      <Image
                        src="/assets/avtar.jpg"
                        alt="avatar"
                        width={34}
                        height={34}
                        className="w-10 h-10 bg-gray-200 rounded-full"
                      />
                      <span className="ml-2">{notice.postedBy.fullName}</span>
                      <span className="ml-2">Published on:&nbsp;</span>
                      <span>
                        {new Date(notice.postedOn).toLocaleDateString("en-GB", {
                          day: "2-digit",
                          month: "short",
                          year: "numeric",
                        })}
                      </span>
                      <span className="mx-2"></span>
                    </div>
                  </div>
                ))}
              </div>
            ) : (
              <div className="flex flex-col items-center justify-center py-12">
                <Image
                  src="/assets/Notice Board Illustration.png"
                  alt="Empty Notice Board"
                  width={120}
                  height={120}
                />
                <p className="text-gray-500 mt-4">Empty Notice Board</p>
              </div>
            )}
          </div>

            {/* Team Members Section */}
            <div className="col-span-2 bg-white rounded-xl p-6">
              <div className="flex justify-between items-center mb-6">
                <h2 className="text-xl font-semibold">
                  Team Members <span>({teamMembers.length})</span>
                </h2>
              </div>
              <div className="grid grid-cols-5 gap-4">
                {/* Team Members Section - Take 4 out of the 5 columns */}
                <div className="grid grid-cols-1 gap-4 col-span-4">
                  <div className="grid grid-cols-3 gap-4">
                    {" "}
                    {/* Changed grid-cols-4 to grid-cols-3 */}
                    {NewTeamMembers.slice(0, 5).map((member, index) => (
                      <div
                        key={index}
                        className="flex items-center space-x-4 p-3 bg-[#FAFAFA]"
                      >
                        <Image
                          src="/assets/avtar.jpg"
                          alt="avtar"
                          width={34}
                          height={34}
                          className="w-10 h-10 bg-gray-200 rounded-full"
                        />
                        <div className="text-md text-[#333333]">
                          <p className="font-medium">{member.name}</p>
                          <p className="text-[#4D4D4D] text-sm">
                            {member.role}
                          </p>
                        </div>
                      </div>
                    ))}
                    <div className="flex items-center space-x-4 p-3 bg-[#FAFAFA] justify-center text-[#333333]">
                      <div className="text-2xl">
                        +
                        {teamMembers.length >= 5 ? +teamMembers.length - 5 : +0}
                      </div>
                    </div>
                  </div>
                </div>

              {/* Button Section - Take the remaining 1 column and take full height and width */}
              <div className="flex items-center justify-center col-span-1 h-full w-full">
                <button className="flex flex-col justify-center items-center px-2 py-2 bg-[#5D56BD] text-white rounded-lg w-full h-full" onClick={()=> setisAddTeamMemberModal(true)}>
                  <div className="-mt-2">
                  <Image src={plusIcon} alt="+" width={48} height={48} className="pt-1.5"/>
                  </div>
                  Manage Team
                </button>
              </div>
            </div>
          </div>
          {/* Milestones Section */}
          <div className="col-span-2 bg-white rounded-xl p-6">
            <div className="flex justify-between items-center mb-6">
              <h2 className="text-xl font-semibold">
                Milestones({milestoneFromBackend.length})
              </h2>
              <button className="px-4 py-2 bg-[#5D56BD] text-white rounded-lg flex items-center" onClick={()=> setisAddMilestone(true)}>
                <Plus className="w-4 h-4 mr-2" />
                Add Milestone
              </button>
            </div>
            
            {milestoneFromBackend.length > 0 ? (
              <div className="space-y-4">
              {milestoneFromBackend.map((milestone, index) => (
                <div
                  key={index}
                  className="flex items-center justify-between bg-[#FAFAFA] p-4 rounded-lg hover:shadow-md transition-shadow "
                >
                  <div className="w-full">
                    <div className="flex items-center space-x-3 w-full">
                      <input
                        type="checkbox"
                        className="w-5 h-5 text-[#5D56BD] rounded"
                        checked={milestone.status === "completed"}
                        onChange={() => {
                          handleMilestoneToogle(milestone._id, milestone.status);
                        }}
                      />
                      <div>
                        <h3 className="font-medium text-gray-900">
                          {milestone.title}
                        </h3>
                        
                      </div>
                    </div>
                    <div className="flex">
                    <div className="flex items-center space-x-2 text-sm text-gray-500 mt-1 ml-8">
                          <Image
                            src="/assets/avtar.jpg"
                            alt="avtar"
                            width={18}
                            height={18}
                            className="bg-gray-200 rounded-full"
                          />
                          <span>{milestone.assignedTo[0].fullName}</span>
                        </div>
                    <div className="flex items-center justify-between ml-auto">
                      <span className="text-sm text-gray-500 ">
                        Due date:  {"  "}
                        {new Date(milestone.dueDate).toLocaleDateString("en-GB", {
                          day: "2-digit",
                          month: "short",
                          year: "numeric",
                        })}
                        <button className="p-2 rounded-lg">
                        <Image
                          src="/assets/Edit Button.png"
                          alt="Edit"
                          width={20}
                          height={20}
                          onClick={()=>handleEditMilestone(milestone._id, milestone.title)}
                        />
                      </button>
                      </span>       
                    </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
            
            ) : (
              <div className="flex flex-col items-center justify-center py-12">
                <Image
                  src="/assets/Milestone Illustration.png"
                  alt="No Milestone"
                  width={120}
                  height={120}
                />
                <p className="text-gray-500 mt-4">No Milestones</p>
              </div>
            )}
          </div>
        </div>
      </div>
      {isEdit && (
        <EditProjectModal isOpen={isEdit} onClose={() => setisEdit(false)} />
      )}
      {isEditmilestoneModal && (
         
        <EditmileStoneModal 
        mutate= {mutate}
        milestoneFromBackend={milestoneFromBackend}
        mileStoneId={mileStoneId} 
        milestoneValue={milestoneValue} 
        onClose={() => setisEditmilestoneModal(false)} />
      )}
       {isAddMilestone && (
          <AddMilestone
          handleMileStoneFetch= {handleMileStoneFetch}
            onClose={() => setisAddMilestone(false)}
            teamMembers={teamMembers}
            setmileStone={setNewMilestone}
            fromOverview={fromOverview}
          />
        )}
        {isAddTeamMemberModalOpen && (
          <AddTeamMembers
            mutate={mutateMembers}
            fromOverview={fromOverview}
            onClose={handleClose}
            onAddMember={handleAddMember}
            teamMembers={teamMembers}
          />
        )}
      </div>
    </>
  );
}
